.skills h3 {
  font-size: 16px;
}
.skills .progress {
  border: none !important;
  box-shadow: none;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  border-radius: 50px;
}
.skills .progress-bar {
  box-shadow: none;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
